@use "../abstract/mixin" as *;

/* Button Style */
button {
    border: none;
    transition: .5s;

    &:focus {
        outline: none;
        box-shadow: none;
    }
}



// solid btn
@mixin btn-style($bg-color, $hover-color, $color) {
    background: hsl(var(--#{$bg-color}));
    color: hsl(var(--#{$color}));
    border: 1px solid transparent;
    font-weight: 500;
    font-size: 16px;
    text-align: center;
    text-transform: capitalize;
    font-family: var(--body-font);
    transition: 0.2s linear;

    &:hover,
    &:active,
    &:focus {
        background: hsl(var(--#{$hover-color})) !important;
        color: hsl(var(--#{$color})) !important;
        border: 1px solid transparent !important;
        box-shadow: 0px 7px 8px 0px hsl(var(--base)/0.13);

    }

    &.pill {
        border-radius: 35px;
    }
}

.btn {
    border-radius: 8px;
    line-height: 1.2;
    position: relative;

    i {
        rotate: -45deg;
        margin-left: 3px;
        transition: 0.2s linear;
    }


    &:focus {
        outline: none;
        box-shadow: none;
    }

    @include slg-screen {
        padding: 6px 10px;
    }


    @include msm-screen {
        padding: 6px 7px;
    }

    &:hover,
    &:focus {
        color: hsl(var(--white));
        border-color: transparent;

        i {
            rotate: 0deg;
        }


        &::after {
            width: 60px;
            transition: 0.3s ease;
        }


        &::before {
            height: 40px;
            transition: 0.3s ease;
        }
    }



    &--sm {
        padding: 4px 12px;
        font-size: 14px;

        &:hover,
        &:focus {
            background-color: hsl(var(--white));
            border: 1px solid transparent;
            color: hsl(var(--white)) !important;
        }

        @include sm-screen {
            padding: 5px 8px;
        }
    }

    &--md {
        padding: 10px 18px;
        font-size: 14px;

        @include sm-screen {
            padding: 6px 12px;
        }
    }

    &--lg {
        padding: 13px 26px;
        font-size: 16px !important;

        @include sm-screen {
            padding: 6px 14px;
            font-size: 16px !important;
        }
    }


    &--base-two {
        background: hsl(var(--base-two));
        color: hsl(var(--white));
        border: 1px solid transparent;
        font-weight: 600;
        font-size: 16px;
        text-align: center;
        text-transform: capitalize;
        font-family: var(--body-font);




        &:hover,
        &:active,
        &:focus {
            background: hsl(var(--base)) !important;
            color: hsl(var(--white)) !important;
            border: 1px solid transparent !important;

        }

        &.pill {
            border-radius: 35px;
        }
    }


    &--base-circle {
        background-size: cover;
        background-repeat: no-repeat;
        color: hsl(var(--base));
        border-radius: 4px;
        position: relative;
        transition: 0.2s linear;


        &::after {
            content: "";
            position: absolute;
            top: 0px;
            left: 0px;
            background-color: hsl(var(--base) / 0.1);
            width: 35px;
            height: 35px;
            border-radius: 50%;
            transition: 0.2s linear;
            z-index: -1;
        }

        &:hover,
        &:focus {
            background: hsl(var(--base));
            color: hsl(var(--white));

            &::after {
                top: 0px;
                left: 0px;
                background-color: hsl(var(--base));
                width: 100%;
                height: 100%;
                border-radius: 4px;
                transition: 0.2s linear;
            }
        }
    }
}


.btn--base {
    @include btn-style(base, base-d-100, black);
}



.btn--primary {
    @include btn-style(primary, primary-d-100, primary);
}

.btn--secondary {
    @include btn-style(secondary, secondary-d-100, secondary);
}

.btn--success {
    @include btn-style(success, success-d-100, success);
}

.btn--danger {
    @include btn-style(danger, danger-d-100, white);
}

.btn--warning {
    @include btn-style(warning, warning-d-100, warning);
}

.btn--info {
    @include btn-style(info, info-d-100, info);
}

.btn--violet {
    @include btn-style(violet, violet-d-100, violet);
}

.btn--dark {
    @include btn-style(dark, dark-l-100, white);


    &::after {
        background-color: hsl(var(--white)/0.3);
    }

    &::before {
        background-color: hsl(var(--white)/0.3);
    }
}









.circle {
    border-radius: 50%;
    width: 55px;
    height: 55px;
    color: hsl(var(--white));
    background-color: hsl(var(--base-two));
    display: flex;
    justify-content: center;
    align-items: center;

    &:hover {
        background-color: hsl(var(--base-d-200));
    }
}



.pill {
    border-radius: 35px !important;
}

.btn-outline {
    background: transparent;
    border: none !important;
    border-radius: 8px;

    &:hover,
    &:focus {
        box-shadow: none !important;
    }
}

// outline btn
@mixin btn-outline-style($color) {
    color: hsl(var(--white));
    border: 1px solid hsl(var(--#{$color})/.2);
    text-transform: capitalize;
    font-weight: 500;

    &:hover,
    &:focus {
        background-color: transparent !important;
        color: hsl(var(--base)) !important;
        border: 1px solid hsl(var(--base));

    }

    &.pill {
        border-radius: 35px;
    }
}


.btn-outline {
    &--base {
        @include btn-outline-style(base);
    }


    &--base-two {
        @include btn-outline-style(base-two);
    }

    &--success {
        @include btn-outline-style(success);
    }

    &--danger {
        @include btn-outline-style(danger);
    }

    &--warning {
        @include btn-outline-style(warning);
    }

    &--info {
        @include btn-outline-style(info);
    }

    &--primary {
        @include btn-outline-style(primary);
    }

    &--secondary {
        @include btn-outline-style(secondary);
    }

    &--dark {
        @include btn-outline-style(dark);
    }

    &--light {
        @include btn-outline-style(white);
    }

    &--violet {
        @include btn-outline-style(violet);
    }
}












// button circle xxl
.btn-circle {
    border-radius: 50%;
    width: 139px;
    height: 139px;
    background: hsl(var(--base));
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 36px;
    color: hsl(var(--white));
    rotate: -45deg;
    position: relative;
    border: 8px solid hsl(var(--white));

    i {
        transition: all 0.3s ease-out;
    }

    &::after {
        content: "";
        width: 100px;
        height: 100px;
        border-radius: 50%;
        position: absolute;
        top: 50%;
        left: 50%;
        border: 1px dashed hsl(var(--white));
        transform: translate(-50%, -50%);
    }

    &:hover {
        color: hsl(var(--white));

        i {
            rotate: 90deg;
            transition: all 0.3s ease-out;
        }

    }
}





.copytext {
    border-radius: 0px 6px 6px 0px !important;
}


@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}


// 
.grp-btn-wrap {
    display: flex;
    justify-content: end;
    align-items: center;
    gap: 12px;
}






.plan--btn {
    border-bottom: 1px solid hsl(var(--white));
    padding-bottom: 5px;
    color: hsl(var(--white));
    font-weight: 600;

    i {
        rotate: -45deg;
        transition: 0.2s linear;
        color: hsl(var(--white));
        margin-left: 3px;
    }

    &:hover {
        color: hsl(var(--base));

        i {
            rotate: 0deg;
            color: hsl(var(--base));
        }
    }
}


.tf-btn {
    border: 1px solid #fff;
    padding: 15px 24px;
    font-family: 'Manrope';
    font-size: 16px;
    font-weight: 400;
    line-height: 19.36px;
    text-transform: capitalize;
    transition: all 0.3s ease-in-out;
    position: relative;
}

.tf-btn i {
    margin-right: 13px;
    margin-left: 3px;
    rotate: 45deg;
    color: hsl(var(--base));
}

.tf-btn:hover {
    color: #161616 !important;
    border: 1px solid hsl(var(--base));
    i {
        color: #161616 !important;
    }
}

.tf-btn::before {
    position: absolute;
    content: "";
    width: 10px;
    height: 10px;
    left: 0;
    bottom: 0;
    background-color: hsl(var(--base));
    z-index: -1;
    transition: all 0.3s;
    opacity: 0;
}

.tf-btn:hover::before {
    width: 100%;
    height: 100%;
    opacity: 1;
}