@use "../abstract/mixin" as *;

/* dashboard Sidebar Menu Start */
.dashboard_profile {
  .sidebar-menu-list__link {
    color: hsl(var(--black));

    &:hover {
      background: hsl(var(--base)/0.1);
    }
  }
}

.sidebar-menu-list__link.active {
  position: relative;


  background: hsl(var(--white)/0.05);
  .icon {
    color: hsl(var(--base));
  }

  .text {
    color: hsl(var(--base));
  }
}

.sidebar-menu {
  padding: 0px 0px 10px 0px;
  transition: 0.2s linear;
  border-radius: 8px;
  border: 1px solid hsl(var(--white) / 0.15);
  background: hsl(var(--section-bg));
  position: fixed;
  top: 10px;
  left: 10px;
  z-index: 99;
  width: 277px;

  .logo-wrapper {
    padding: 24px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
  }



  &.show-sidebar {
    transform: translateX(0);
  }

  @include lg-screen {
    position: fixed;
    left: 0;
    top: 0;
    min-width: 320px;
    transform: translateX(-100%);
    z-index: 9992;
    border-radius: 0;
    background-color: hsl(var(--white));
  }

  &__close {
    position: absolute;
    top: 12px;
    right: 12px;
    color: hsl(var(--base));
    border: 1px solid hsl(var(--base));
    width: 35px;
    height: 35px;
    line-height: 35px;
    text-align: center;
    font-size: 18px;
    transition: .2s linear;
    cursor: pointer;
    display: none;
    z-index: 9;
    border-radius: 3px;

    @include lg-screen {
      display: block;
    }

    &:hover,
    &:focus {
      background-color: hsl(var(--base));
      border-color: hsl(var(--base));
      color: hsl(var(--black));
    }
  }


  &-list {
    height: 100vh;
    overflow-y: auto;
    padding: 0px 0px 120px 0px;
    position: relative;



    &__item {
      margin: 0px 20px 8px 20px;

      &.has-dropdown {
        &.active {
          background-color: hsl(var(--white)/0.05);
          border-radius: 4px;
          >a {
            color: hsl(var(--base-two));

            &:after {
              transform: rotate(90deg);
              margin-left: 14px;
              color: hsl(var(--white));
            }
          }
        }

        >a {
          &:after {
            font-family: "Font Awesome 5 Free";
            font-weight: 900;
            content: "\f105";
            position: absolute;
            top: 20px;
            right: 20px;
            transition: 0.1s linear;
            margin-left: 14px;
            color: hsl(var(--white));
          }
        }
      }
    }

    &__link {
      display: flex;
      align-items: center;
      text-decoration: none;
      gap: 12px;
      position: relative;
      padding: 16px 20px 16px 20px;
      width: 100%;
      color: hsl(var(--white)) !important;

      &:hover {
        background: hsl(var(--black)/0.05);
        color: hsl(var(--base));
        border-radius: 4px;
      }

      .icon {
        display: block;
        width: 30px;
        height: 30px;
        background-color: hsl(var(--white)/0.05);
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 16px;
        border-radius: 4px;
        animation: swing ease-in-out 0.5s 1 alternate;
        color: hsl(var(--base));
      }
    }
  }
}

/*=============== Sidebar Submenu Start ===============*/
.sidebar-submenu {
  display: none;
  position: relative;

  &.open-submenu {
    display: block;
  }

  &-list {
    padding: 5px 0;

    &__item {
      position: relative;

    

      &.active {
        &::before {
          background: hsl(var(--base)) !important;
        }

        >a {
          color: hsl(var(--base));
        }
      }
    }

    &__link {
      margin: 0px 20px 8px 24px !important;
      padding: 12px 0px 12px 16px;
      display: block;
      color: hsl(var(--white));
      border-bottom: 0 !important;
      &:hover{
        background: hsl(var(--black)/0.08);
        border-radius: 4px;
        color: hsl(var(--base));
      }

      &.active{
        border-bottom: 1px solid hsl(var(--base)) !important;
        border-radius: 8px;
        &:hover{
          background: transparent !important;
        }
      }
    }
  }
}

@keyframes swing {
  0% {
    transform: rotate(0deg);
  }

  10% {
    transform: rotate(10deg);
  }

  30% {
    transform: rotate(0deg);
  }

  40% {
    transform: rotate(-10deg);
  }

  50% {
    transform: rotate(0deg);
  }

  60% {
    transform: rotate(5deg);
  }

  70% {
    transform: rotate(0deg);
  }

  80% {
    transform: rotate(-5deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

/*========  responsive sidebar menu  =======*/
.sidebar-menu-wrapper {
  width: 300px;
  height: 100vh;
  background-color: hsl(var(--dark));
  position: fixed;
  z-index: 15;
  left: -320px;
  transition: all .4s ease-in-out;

  &.show {
    left: 0;
  }

  .top-close {
    margin-top: 21px;
    text-align: end;
    margin-right: 24px;

    i {
      font-size: 22px;
      color: hsl(var(--white));
    }
  }

  .header-wrapper .logo-wrapper img {
    width: 130px;
    margin-left: 6px;

    @include md-screen {
      margin-left: 15px;
    }

    @include msm-screen {
      margin-left: 6px;
    }
  }
}

.sidebar-menu-list__item .btn.btn--base {
  border: 1px solid hsl(var(--white));
}

.sidebar-menu-list__item {
  a {
    span {
      color: hsl(var(--white));
    }
  }
}