@use "../abstract/mixin" as *;

.table {
	margin: 0;
	font-size: 15px;
	border-collapse: separate;
	border-spacing: 0px 0px;
	border-radius: 10px;

	thead {
		tr {
			th {
				background: hsl(var(--white)/.04);
				text-align: center;
				font-size: 16px;
				padding: 15px;
				color: hsl(var(--white));
				font-family: var(--heading-font);
				font-weight: 500;
				max-width: 170px;
				white-space: nowrap;
				border: 0;

				&:first-child {
					text-align: left;
					border-top-left-radius: 10px;
					border-bottom-left-radius: 10px;
				}

				&:last-child {
					border-top-right-radius: 10px;
					border-bottom-right-radius: 10px;
					text-align: right;
				}
			}

			.tbl-text-right {
				text-align: right !important;
			}

			.tbl-text-center {
				text-align: center !important;
			}

			.tbl-text-left {
				text-align: left !important;
			}

			.tbl-text-center {
				&:last-child {
					text-align: center;
				}
			}
		}
	}

	tbody {
		border: 0 !important;
		color: hsl(var(--white)/0.8);

		tr {


			// &:nth-child(odd) {
			//     background: hsl(var(--black)/0.07);
			// }
			&:last-child {
				td {
					&:first-child {
						border-radius: 0 0 0px 5px;

					}

					&:last-child {
						border-radius: 0 0 5px 0;
						border-left: 0;
					}

					border: 0;
				}
			}

			td {
				vertical-align: middle;
				border-width: 1px;
				border: 0;
				font-family: var(--body-font);
				font-size: 16px;
				color: hsl(var(--white)/0.8);
				max-width: 170px;
				font-weight: 400;
				text-align: left;
				padding: 16px 12px;
				text-align: center;
				border-bottom: 1px solid hsl(var(--white) / .08);

				span {
					color: hsl(var(--white)/0.8);
					font-weight: 500;
				}

				p {
					font-size: 14px;
				}

				.table--img {
					max-width: 32px;
					max-height: 32px;
					border-radius: 50%;
				}

				&::before {
					content: attr(data-label);
					font-family: var(--heading-font);
					font-size: 15px;
					color: hsl(var(--white));
					font-weight: 500;
					display: none;
					width: 50% !important;
					text-align: left;
				}

				&:first-child {
					text-align: left;
				}

				&:last-child {
					text-align: right;
				}

			}

			.tbl-text-right {
				text-align: right;
			}

			.tbl-text-center {
				text-align: center;
			}

			.tbl-text-left {
				text-align: left;
			}

			.tbl-text-center {
				&:last-child {
					text-align: center !important;
				}
			}
		}
	}
}

.table>:not(caption)>*>* {
	background-color: transparent;
}

.table--responsive--md {
	@include sm-screen {
		thead {
			display: none;
		}

		tbody {
			tr {
				display: block;

				td {
					display: flex;
					align-items: center;
					justify-content: space-between;
					gap: 35px;
					text-align: right;
					padding: 10px 15px;
					border: none;
					border-bottom: 1px solid hsl(var(--white) / .08);
					max-width: unset;

					&:last-child {
						border: none;
					}

					&:first-child {
						text-align: right;
					}

					&::before {
						display: block;
					}
				}
			}
		}
	}

	// @include sm-screen {
	//     tbody {
	// 		tr {
	// 			td {
	// 				border: 0;
	// 			}
	// 		}
	// 	}
	// }
}

.table--responsive--lg {
	@include md-screen {
		thead {
			display: none;
		}

		tbody {
			tr {
				display: block;

				td {
					display: flex;
					align-items: center;
					justify-content: space-between;
					gap: 0px;
					text-align: right;
					padding: 10px 15px;
					border: none;
					border-bottom: 1px solid hsl(var(--white) / .08) !important;
					max-width: unset;
					flex-wrap: wrap;

					&:last-child {
						border: none !important;
					}

					&:first-child {
						text-align: right;
					}

					&::before {
						display: block;
					}
				}
			}
		}
	}

}


