.table-wrapper {
	@include md-screen {
		.table--responsive--lg tbody tr td {
			display: block;
		}
	}
}


@media(max-width: 992px) {
	.table {
		tbody {
			tr {
				&:nth-child(even) {
					border-bottom: 1px solid hsl(var(--dark) / 0.03);
					background: hsl(var(--base)/0.03);
				}
			}
		}
	}
}




.table-item {
	display: flex;
	align-items: center;
}
.table-item__thumb {
    width: 65px;
    height: 65px;
    border-radius: 5px;
    margin-right: 10px;
}
.table-item__thumb img {
    height: 100% !important;
    width: 100% !important;
    object-fit: cover !important;
}
.table-item__content {
    width: calc(100% - 65px);
}
.table-item__content .title-two {
    font-size: 16px;
    line-height: 0;
    margin-bottom: 0;
}

.table-item__content .price {
    font-size: 18px;
    margin-bottom: 0;
}

// order summary
.cart-total ul {
    border: 0px solid hsl(var(--white) / 0.1);
}
.cart-total ul li.total {
    font-size: 18px;
    font-weight: 500;
    color: hsl(var(--base));
}
.cart-total ul li {
    list-style: none;
    font-size: 15px;
    color: #6f7172;
    padding: 10px 0px;
    border-bottom: 1px solid hsl(var(--white) / 0.1);
    font-weight: 400;
    color: hsl(var(--white) / 0.9);
}

.cart-total ul li span {
    float: right;
}




                                                 

