@use "../abstract/mixin" as *;

.blog-sidebar-wrapper{
    position: sticky;
    top: 150px;
}

.social-list__item .social-list__link {
    border: 0px !important;
    color: hsl(var(--dark));
    &:hover {
        color: hsl(var(--base)) !important;
    }
}

.blog-sidebar {
    margin-bottom: 30px;
   
    &:last-of-type {
        margin-bottom: 0;
    }
    @include msm-screen {
        padding: 20px 15px;
    }



    &--title {
        margin-bottom: 40px;

        &::after{
            content: "";
            position: absolute;
            top: 40px;
            left: 0;
            width: 100%;
            height: 2px;
            background: linear-gradient(90deg, hsl(var(--base)) 0.01%, hsl(var(--body-color)) 80.99%);
        }
    }



    .categorie--list{
        li{
            a{
                .title{
                    &:hover{
                        color: hsl(var(--base));
                        text-decoration: underline;
                    }
                }
            }
        }
    }
}




.latest-blog {

    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid hsl(var(--black)/.1);

    &:last-of-type {
        padding-bottom: 0px;
        margin-bottom: 0px;
        border-bottom: 0;
    }
    &--thumb {
        width: 80px;
        max-height: 80px;
        display: flex;
        border-radius: 5px;
        overflow: hidden;
        @include xsm-screen {
            width: 60px;
        }
        a {
            display: block;
            height: 100%;
            width: 100%;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 5px;
        }
    }
    &--content {
        width: calc(100% - 80px);
        padding-left: 15px;
        @include xsm-screen {
            width: calc(100% - 60px);
        }
    }
    &--title {
        margin-bottom: 5px;
     
        &:hover {
            color: hsl(var(--base));
            text-decoration: underline;
        }
        
    }

}
