  /*Blog Details */
  @use "../abstract/mixin" as *;

  .blog-details {
      overflow: hidden;


      ul {
          padding-left: 12px;

          li {
              list-style: none;
              margin-bottom: 12px;
              color: hsl(var(--white) / 0.75);
          }
      }



      @include md-screen {
          margin-right: 0px;
      }

      .blog-item--content {
          padding: 35px 0px;
          margin-top: 10px;
      }

      .blog-item--content {
          background-color: transparent;
      }

      .blog-item {
          box-shadow: none;

          &--thumb {
              height: 450px;
              max-height: 100%;

              @include md-screen {
                  height: 350px;
              }

              @include sm-screen {
                  height: 300px;
              }

              img {
                  border-radius: 12px;
              }
          }

          &--content {
              padding-bottom: 0;
          }

          &--content2 {
              bottom: 0px;
              left: 0px;
              background-color: hsl(var(--white));
              padding: 38px 38px 0px 0px;

              .date--wrap {
                  padding-bottom: 16px;

                  .icon {
                      width: 48px;
                      height: 48px;
                      background-color: hsl(var(--base));
                      border-radius: 50%;

                      i {
                          color: hsl(var(--white));
                      }
                  }
              }


              .text-list {
                  padding-top: 16px;
              }
          }
      }


      &--content {
          padding: 20px 0px;
      }

      &--title {
          margin-bottom: 15px;
      }

      &--desc {
          margin-bottom: 15px;
      }

      .blog-bottom-wrap {
          border-top: 0;
          padding: 20px 25px 0px;
      }
  }

  .blog-details--share {
      ul {
          li {
              a {
                  width: 44px;
                  height: 44px;
                  border-radius: 50%;
                  background: hsl(var(--black)/.05);
                  border: 1px solid hsl(var(--white)/.1);

                  i {
                      color: hsl(var(--base));
                  }

                  &:hover {
                      background: hsl(var(--base));

                      i {
                          color: hsl(var(--black));
                      }
                  }
              }
          }
      }
  }

  .blog-details--quote1 {
      background-color: hsl(var(--base) / .1);
      padding: 40px;
      border-radius: 4px;
      margin-bottom: 20px;
      font-size: 22px;
      font-weight: 500;
      margin-top: 30px;
      color: hsl(var(--white));
  }

  .blog-details--quote2 {
      background-color: hsl(var(--base) / .1);
      padding: 40px;
      border-radius: 4px;
      margin-bottom: 20px;
      font-size: 22px;
      font-weight: 500;
      margin-top: 30px;
      color: hsl(var(--black)/0.7);
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      text-align: center;
      position: relative;

      &::before {
          content: "\f10d";
          position: absolute;
          font-family: FontAwesome;
          font-weight: 900;
          font-size: 80px;
          bottom: -12px;
          left: 0px;
          z-index: -1;
          opacity: 0.4;
          transform: translateX(39%);
      }

      &::after {
          content: "\f10e";
          position: absolute;
          font-family: FontAwesome;
          font-weight: 900;
          font-size: 120px;
          top: -24px;
          right: 10%;
          z-index: -1;
          opacity: 0.1;
          transform: translateX(39%);
          color: hsl(var(--base-two));
      }

  }



  .blog-rel--thumb {
      width: 100%;
      height: 302px;
  }