@use "../abstract/mixin" as *;

.footer-area {
    position: relative;
    background-color: transparent;


    .bg--thumb-one {
        top: 50%;
        left: 0;
        transform: translate(0%, -50%);
        z-index: -1;
    }

    .bg--thumb-two {
        top: 50%;
        right: 0;
        transform: translate(0%, -50%);
        z-index: -1;
    }

    .bg-thumb {
        position: absolute;
        width: 350px;
        top: 20px;
        left: 14%;
    }

}

.footer-item {
    .latest-blog__title a {
        color: hsl(var(--black));
    }

    .latest-blog {
        padding-bottom: 15px;
        margin-bottom: 15px;
        border-bottom: 1px solid hsl(var(--black)/0.1);
    }

    span.latest-blog__date {
        color: hsl(var(--black)/0.6);
    }

    &--heading{
        font-size: 120px;
        margin-bottom: 30px;
        font-style: italic;
    }

    &--desc {
        color: hsl(var(--white));
        margin-bottom: 36px;
        width: 100%;
    }

    &--logo {
        margin-bottom: 20px;

        a {
            img {
                width: 100%;
                height: 100%;
                max-width: 120px;
                max-height: 64px;
                filter: brightness(1);
            }
        }
    }

    &--title {
        color: hsl(var(--white));
        position: relative;
        font-weight: 500;
        font-size: 20px;
        padding-bottom: 20px;
        margin-bottom: 20px;

        &::after {
            content: "";
            position: absolute;
            top: 40px;
            left: 0;
            width: 100px;
            height: 2px;
            background: linear-gradient(90deg, hsl(var(--base)) 0.01%, hsl(var(--body-color)) 80.99%);
        }


    }

    .social-list {
        margin-top: 10px;
        display: flex;
        gap: 12px;

        @include md-screen {
            margin-top: 20px;
        }

        @include msm-screen {
            margin-top: 15px;
        }
    }

    .social-list--item .social-list__link {
        font-size: 20px;
        border: 0px;
        color: hsl(var(--base));

        &:hover {
            color: hsl(var(--white)) !important;
        }
    }
}

// social icon
.icon-wrapper {
    .icon {
        background: transparent;
        transition: 0.2s linear;
        border: 1px solid hsl(var(--white)/0.2);

        i {
            color: hsl(var(--white));
            transition: 0.2s linear;
            font-size: 16px;
        }

        &:hover {
            background: hsl(var(--base));
            transition: 0.2s linear;

            i {
                color: hsl(var(--white));
                transition: 0.2s linear;
            }
        }
    }

    div {
        width: 44px;
        height: 44px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

// Footer List Item
.menu {
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin: -5px 0;

    &--item {
        display: block;
        padding: 8px 0;
    }

    &--link {
        position: relative;
        color: hsl(var(--white)/0.8);
        display: flex;
        align-items: center;
        gap: 12px;

        i {
            font-size: 16px;
            color: hsl(var(--white)/.7);
        }

        &:hover {
            color: hsl(var(--white));
            margin-left: 4px;
        }
    }
}

// Footer Contact
.footer-contact-menu {
    margin: -6px 0;

    &__item {
        display: flex;
        padding: 1px 0;

        p {
            color: hsl(var(--black));
        }

        &-icon {
            width: 15px;
            color: hsl(var(--white));
            font-size: 15px;
        }

        &-content {
            width: calc(100% - 15px);
            padding-left: 15px;
        }
    }
}






.bottom-footer {
    position: relative;
    background-color: hsl(var(--white)/0.02);

    .bottom-footer-text {
        color: hsl(var(--white));

        @include md-screen {
            text-align: center;
        }

        @include xsm-screen {
            font-size: 14px;
        }
    }
}







// footer contact info
.footer-contact-info {
    p {
        color: hsl(var(--black)/0.8);
        width: 100%;

        a {
            color: hsl(var(--black)/0.8);
        }
    }

    i {
        font-size: 20px;
        color: hsl(var(--base));
    }
}

.footer-logo-dark {
    display: none;
}