// bg image
.bg--img {
  background-size: cover !important;
  background-repeat: no-repeat !important;
  background-position: center !important;
}

.bg--img-2 {
  background-repeat: no-repeat !important;
  background-position: top !important;
  background-size: cover !important;
}

.bg--transparent {
  background: transparent !important;
}

// cover fit image
.fit--img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.position--center {
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

// overflow hidden
.overflow--hidden {
  overflow: hidden;
}

// cursor pointer

.cursor-pointer {
  cursor: pointer;
}

// text capitalize

.text--capitalize {
  text-transform: capitalize !important;
}

// container wrap
.container--wrap {
  padding: 0px 40px;
}

// dashboard tap balance
.tap--balance {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  gap: 8px;
  background-color: hsl(var(--base) / 0.1);
  padding: 16px 12px 16px 8px;
  border-radius: 8px;
  width: 194px;
  transition: 3s ease;
  transition: flex-direction 0.3s ease;
  position: relative;

  &.goLeft {
    .tap-circle--icon {
      -webkit-animation: leftRight 0.9s forwards;
      animation: leftRight 0.9s forwards;
    }
  }

  &.goRight {
    .tap-circle--icon {
      -webkit-animation: rightLeft 0.9s forwards;
      animation: rightLeft 0.9s forwards;
    }
  }

  .tap-circle--icon {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: hsl(var(--base));
    color: hsl(var(--black));
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    position: absolute;
    z-index: 1;
    cursor: pointer;
  }

  @keyframes rightLeft {
    0% {
      right: 0%;
    }

    100% {
      right: 80%;
    }
  }


  @keyframes leftRight {
    0% {
      left: 0%;
    }

    100% {
      left: 80%;
    }
  }


  .op--0 {
    opacity: 0;
    transition: 0.1s linear;
  }

  .textt {
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    text-align: center;
    margin-left: 26px;

  }

  .balance {
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    text-align: center;



  }
}

.transition-animation {
  animation-name: fadeInOut;
  animation-duration: 2s;
}

//dashboard refer link 
.refer-link--wrap {
  border: 1px dashed hsl(var(--base-two)/0.7);
  padding: 8px 12px;
  padding-right: 50px;
  border-radius: 8px;
  position: relative;

  .text {
    font-size: 16px;
    font-weight: 400;
  }

  .copy--btn {
    position: absolute;
    top: 4px;
    right: 10px;
    font-size: 18px;
    color: hsl(var(--base-two));
  }
}



// fade animation class
.fade-animation {
  animation-name: fadeInOut;
  animation-duration: 2s;
}


// base circle icon
.circle--icon {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: hsl(var(--base));
  color: hsl(var(--white));
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
}


.filter--box {
  display: none;
}

// custom column
@media (max-width:1920px) and (min-width:1799px) {
  .xxxl {
    flex: 0 0 auto;
    width: 16.66666667%;
  }
}

@media (max-width:1800px) and (min-width: 1562px) {
  .xxxl {
    flex: 0 0 auto;
    width: 20%;
  }
}

@media (max-width:1563px) and (min-width:1440px) {
  .xxxl {
    width: 25% !important;
  }
}


// offcanvas

.offcanvas {
  background: hsl(var(--body-color)) !important;
  width: 300px !important;

  .offcanvas-header {
    .btn-close {
      opacity: 1;
    }

    .logo {
      max-width: 50%;
    }
  }

  .offcanvas-body {
    padding: 32px 0px;


    .user-info {
      padding: 24px 16px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 12px;

      a {
        h4 {
          margin: 0px;
          color: hsl(var(--white));
        }
      }

      .user-thumb {
        height: 80px;
        width: 80px;
        border-radius: 50%;
        z-index: 99;

        img {
          height: 100%;
          width: 100%;
          border-radius: 50%;
          object-fit: cover;
        }

      }
    }

    .side-Nav {

      padding: 0px 24px;

      li {
        position: relative;
        border-bottom: 1px solid hsl(var(--base)/.2);

        button {
          color: hsl(var(--white));
          height: 50px;
        }

        &:last-child {
          a {
            border-bottom: none !important;
          }
        }

        a {
          display: flex;
          align-items: center;
          font-size: 16px;
          gap: 16px;
          height: 50px;

          color: hsl(var(--white));

          p {
            font-size: 16px;
          }
        }

        .active {
          color: hsl(var(--base));
        }
      }
    }
  }

  .search-box {
    padding: 24px 0px;
    display: flex;
    justify-content: center;

    .form--control {
      font-weight: 500;
      outline: none;
      width: 70%;
      padding: 10px 44px;
      line-height: 1;
    }

    .search-btn {
      padding: 10px 20px;
    }
  }
}

@media (max-width: 770px) {
  .offcanvas {
    .search-box {
      padding: 16px 12px;
      display: flex;
      justify-content: center;

      .form--control {
        font-weight: 500;
        outline: none;
        width: 70%;
        padding: 10px !important;
        background-color: hsl(var(--body-color)/0.1);
        border: 1px solid hsl(var(--base));
        padding-right: 0px !important;
        line-height: 1;
      }

      .search-btn {
        border: 1px solid hsl(var(--base));
        padding: 10px 10px !important;
        color: hsl(var(--base));
      }
    }
  }
}


// owner profile

.owner--profile {
  
  .profile--banner {
    width: 100%;
    height: 368px;
  }


  .profile--info {
    margin-top: -100px;
    .thumb--wrap {
      img{
        width: 166px;
