}


// section border

.section--border {

  position: relative;
  width: 64.5%;
  height: 1px;
  background-color: hsl(var(--black));
  top: 50%;
  left: 50%;
  bottom: -100px;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);


  &::after {
    content: "";
    position: absolute;
    bottom: 10px;
    left: -30px;
    width: 127px;
    height: 1px;
    background-color: hsl(var(--black));
  }

  &::before {
    content: "";
    position: absolute;
    bottom: -10px;
    right: -30px;
    width: 127px;
    height: 1px;
    background-color: hsl(var(--black));
  }


}





.profile-menu--wrap {
  padding-bottom: 8px;

  li a.active {
    color: hsl(var(--base));
    position: relative;

    &::after {
      content: "";
      position: absolute;
      left: 0px;
      bottom: -8px;
      width: 100%;
      height: 2px;
      background-color: hsl(var(--base));

    }
  }
}

.portfolio-item--wrap {
  .thumb--wrap {
    width: 100%;
    height: 352px;
  }
}








