.error--page{
    padding: 130px 0px;
    background: linear-gradient(180deg, #09F -28%, #E9F7FF 25%, #E9F7FF 75%, #09F 156%);

    .error-wrap{

        .error--text{
            span{
                font-size: 80px;
                font-weight: 700;
                &:nth-child(1){
                    color: hsl(var(--base));
                }
                &:nth-child(2){
                    color: hsl(var(--danger));
                }
                &:nth-child(3){
                    color: hsl(var(--base));
                }
            }
        }

        .title{
            font-size: 80px;
            font-weight: 700;
        }
        .desc{
            a{
                text-decoration: underline;
                color: hsl(var(--base));
            }
        }
    }
}