

// count card
.count-card{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border: 1px solid hsl(var(--white)/.2);
    transition: 0.3s linear;
    padding: 32px 16px;
    border-radius: 6px;

    &:hover{
      border: 1px solid hsl(var(--base));
    }
  
    .count-number-wraper{
      display: flex;
      align-items: center;
     
      .amount{
        font-size: 48px;
        font-weight: 700;
        -webkit-text-fill-color: hsl(var(--black));
        -webkit-text-stroke-width: 1px;
        -webkit-text-stroke-color: hsl(var(--white));
        line-height: 1.55;
        margin: 0px;
      }
    }
    .subtitle{
        font-size: 14px;
        font-weight: 500;
        color: hsl(var(--black));
        text-transform: uppercase;
    }
  }