@use "../abstract/mixin" as *;

.header {
    position: absolute;
    z-index: 5;
    padding: 0px;
    width: 100%;
    background-color: hsl(var(--section-bg));


    @include md-screen {
        top: 0px;
        position: relative;
        left: 0;
        right: 0;
        z-index: 7;

        &::-webkit-scrollbar {
            width: 5px;
            height: 5px;
        }

        &::-webkit-scrollbar-thumb {
            border-radius: 0px;
        }
    }
    .header--top {
        padding: 12px 0px;
        border-bottom: 1px solid hsl(var(--white) / .1);

        .icon--wrap {
            .icon {
                width: 32px;
                height: 32px;
                border-radius: 50%;
                border: 1px solid hsl(var(--white) / .1);
                i {
                    font-size: 16px;
                }
            }
        }
    }


    &.fixed-header {
        background-color: hsl(var(--body-color));
        position: fixed;
        top: 0px;
        animation: slide-down 0.8s;
        width: 100%;
        box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.16);
        -webkit-box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.16);
        -moz-box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.16);
        padding: 0px;
        z-index: 99;
        border-bottom: 1px solid hsl(var(--white)/0.1);

        .header-wrapper .main-menu li a {
            color: hsl(var(--white));
        }

        .login--btn {
            border: 1px solid hsl(var(--white));
            color: hsl(var(--white));

        }

        .language-box .select {
            color: hsl(var(--white));
        }

        .language-box i {
            color: hsl(var(--white));
        }
    }

}

.header-main-area {
    .container-fluid {
        padding: 0 100px;

        @include xl-screen {
            padding: 0 60px;
        }

        @include msm-screen {
            padding: 0 25px;
        }
    }
}



.scrolled {
    display: none !important;
}


.header-wrapper {

    display: flex;
    justify-content: space-between;
    align-items: center;




    .ham__menu {
        color: hsl(var(--white));
        display: none;

        @include md-screen {
            display: block !important;
        }
    }

    .logo-wrapper {
        img {
            max-width: 140px;
            filter: brightness(1);

            @include md-screen {
                width: 160px;
            }

            @include xsm-screen {
                width: 112px;
            }
        }
    }


    .search--bar__wrap {
        .form--control {
            background-color: hsl(var(--white)/0.1);
            width: 336px;
            border: 0 transparent;
        }

        button {
            position: absolute;
            top: 4px;
            right: 4px;
            background-color: hsl(var(--base));
            padding: 7px 16px;
            border-radius: 8px;
            color: hsl(var(--white));
        }
    }



    .main-menu {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 12px;

        @include md-screen {
            display: none !important;
        }

        @include lg-screen {
            margin-left: 0px;
        }

        li {
            transition: all .4s;

        }

        a {
            font-size: 16px;
            color: hsl(var(--white));
            font-weight: 600;
            position: relative;
            text-transform: uppercase;
            margin-right: 22px;
            padding: 30px 0px;

            @include md-screen {
                padding: 15px 0;
            }

            @include msm-screen {
                padding: 10px 0px !important;
            }


            i {
                font-size: 16px;
                rotate: -45deg;
                margin-right: 6px;
                transition: 0.2s linear;
            }

            &:hover {
                color: hsl(var(--base));

                i {
                    rotate: 0deg;
                }
            }



            &.active {
                color: hsl(var(--base));

                i {
                    color: hsl(var(--base));
                }
            }

            @include xl-screen {
                font-size: 14px;
            }

            @include lg-screen {
                padding: 0px;
                font-size: 14px;
            }

        }
    }








    @media (max-width: 770px) {

        .menu-list-wrapper {
            display: block !important;

            ul {
                gap: 0px;
            }
        }
    }


    @media (max-width: 620px) {

        .menu-list-wrapper {

            ul:last-child {
                display: none;
            }
        }
    }


    .count-item {
        position: absolute;
        height: 19px;
        width: 19px;
        background: hsl(var(--base));
        text-align: center;
        border-radius: 50%;
        font-size: 13px;
        color: hsl(var(--black));
        top: -7px;
        right: 7px;
        font-weight: 600;
        line-height: 19px;

        @include md-screen {
            top: -4px;
        }
    }
}










.header.fixed-header {
    .header-wrapper .main-menu a {
        color: hsl(var(--base));

        &:hover {
            color: hsl(var(--base));
        }

        &.active {
            color: hsl(var(--base));
        }
    }

    .light-dark-btn-wrap .mon-icon {
        color: hsl(var(--black));
    }

    .menu-search-wrap .menu-search-wrap--search-icon i {
        font-size: 17px;
        color: hsl(var(--black));
    }

    // .header-wrapper .ham__menu{
    //     color: hsl(var(--black));
    // }
}









// Language Box
.language-box {
    display: flex;
    align-items: center;
    position: relative;

    i {
        color: hsl(var(--white));
        font-size: 17px;
        margin-right: 5px;
    }

    .select {
        color: hsl(var(--white));
        background-color: transparent;
        border: 1px solid transparent;
        padding: 3px 0px;
        font-size: 14px;

        option {
            background-color: hsl(var(--dark));
        }
    }
}

// header login button
.login--btn {
    color: hsl(var(--black));
    font-weight: 600;
    padding: 10px 18px;
    border-radius: 5px;
    border: 1px solid hsl(var(--black));

    &:hover {
        background-color: hsl(var(--base));
        color: hsl(var(--black));
    }
}


.login-lng li .cart--btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    color: hsl(var(--white));
    border: 1px solid hsl(var(--base));
}

.login-lng li .count--item {
    top: -10px;
    right: -10px;
    color: hsl(var(--white));
    background: hsl(var(--base));
    border-radius: 20px;
    font-size: 9px;
    font-weight: 700;
    padding: 2px 4px;
    border: 1px solid hsl(var(--white));
    width: 78%;
    text-align: center;
}



.header-wrapper .main-menu .links--wrap {
    position: relative;
}



.header-wrapper .main-menu .links--wrap .sub-menu {
    opacity: 0;
    transition: 0.01s linear;
    visibility: hidden;
    width: 220px;
    height: auto;
    border-radius: 4px;
    position: absolute;
    background: hsl(var(--section-bg));
    top: 82px;
    left: -2px;
    z-index: 1;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
    padding: 12px;
    display: flex;
    flex-direction: column;
    gap: 8px;
}


.header-wrapper .main-menu .links--wrap .sub-menu a {
    display: block;
    font-weight: 600;
    border-bottom: 1px solid hsl(var(--white) / 0.1);
    padding-top: 0 !important;
    padding-bottom: 12px !important;
    width: 100%;
    text-transform: capitalize !important;
}

.header-wrapper .main-menu .links--wrap .sub-menu a:last-child {
    padding-bottom: 0 !important;
    border-bottom: 0 !important;
}


.header-wrapper .main-menu .links--wrap:hover .sub-menu {
    visibility: visible;
    opacity: 1;
    transition: 0.1s linear;
}