@use "../abstract/mixin" as *;

.breadcrumb {
    position: relative;
    padding: 280px 0px 145px 0px;
    z-index: 1;
    overflow: hidden;

    .bg--thumb-one {
        top: 70%;
        left: 0;
        transform: translate(0%, -50%);
    }

    .bg--thumb-two {
        top: 50%;
        right: 0;
        transform: translate(0%, -50%);
    }



    @include md-screen {
        padding: 100px 0px 40px 0px;
    }

    @include sm-screen {
        padding: 100px 0px 30px 0px;
    }

    .content--wrap {
        background-color: hsl(var(--white)/0.1);
        border-radius: 18px 18px 0px 0px;
        padding: 12px 12px 0px 12px;

        .content {
            background-color: hsl(var(--hero-bg));
            border-radius: 18px 18px 0px 0px;
            padding: 80px 60px 0px 60px;
        }
    }

    .breadcrumb-bg--img {
        position: absolute;
        top: -60px;
        left: 0px;
    }


    &--wrapper {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        gap: 24px;
        width: 100%;
    }

    &--title {
        font-size: 48px;
        margin: 0px !important;
        color: hsl(var(--white));
        text-align: center;
        line-height: 1.25;
        font-weight: 700;
        flex-shrink: 0;
    }


    &--list {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: 12px;
        padding: 6px 18px;
        border-radius: 32px;
        border: 1px solid hsl(var(--white) / 0.5);
    }

    &--item {
        color: hsl(var(--white));
        padding-right: 4px;
        font-weight: 400;
        font-size: 18px;

        @include msm-screen {
            font-size: 15px;
        }

        &--text {
            color: hsl(var(--base));
        }
    }

    &--icon {
        color: hsl(var(--base));
        line-height: 28px;
        font-size: 18px;
        rotate: -45deg;
    }

    &--link {
        color: hsl(var(--white)) !important;
        font-weight: 600;

        &:hover {
            color: hsl(var(--base));
        }
    }
}


.animate--thumb {
    .element1 {
        position: absolute;
        top: -28%;
        left: 35%;
        -ms-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
    }

    .element2 {
        position: absolute;
        top: -10%;
        left: 35%;
        -ms-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
    }
}