.pagination {
    flex-wrap: wrap;
    justify-content: center;

    .page-item {
        .page-link {
            border: 1px solid hsl(var(--white)/0.5);
            margin: 0 5px;
            border-radius: 50%;
            height: 40px;
            width: 40px;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: transparent;
            font-weight: 500;
            padding: 0;
            color: hsl(var(--white));

            &.active,
            &:hover {
                color: hsl(var(--black));
                background-color: hsl(var(--base));
            }

            &:focus {
                box-shadow: none;
            }
        }

        &.active .page-link {
            color: hsl(var(--black));
            background-color: hsl(var(--base));
        }
    }
}



.pagination--wrap {
    border-top: 1px solid hsl(var(--white)/.2);
    border-bottom: 1px solid hsl(var(--white)/.2);
    padding: 20px 0;


    .page--title {
        font-size: 44px;
        font-weight: 700;
        -webkit-text-stroke-width: 1px;
        -webkit-text-stroke-color: hsl(var(--white)/.2);
        color: transparent;
    }

    .prev--btn-wrap {
        .prev--btn {
            width: 70px;
            min-width: 70px;
            height: 70px;
            border-radius: 50%;
            border: 1px solid hsl(var(--white));
        }
    }

    .next--btn-wrap {
        .next--btn {
            width: 70px;
            min-width: 70px;
            height: 70px;
            border-radius: 50%;
            border: 1px solid hsl(var(--white));
        }
    }
}