// Slick Arrow & Dots css Start 
@use "../abstract/mixin" as *;

.slick-initialized.slick-slider {
  margin: 0;

  .slick-track {
    display: flex;
    gap: 12px;
    padding: 20px 0px;
  }

  .slick-slide {
    padding: 0px 0px 0px 0px;
    height: auto !important;
  }

}

.slick-arrow {
  position: absolute;
  z-index: 1;
  top: 50%;
  right: 0px;
  transform: translateY(-50%);
  border: 1px solid hsl(var(--white)/.2);
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.2s linear;
  color: hsl(var(--white));
  font-size: 20px;
  margin: 0px;


  @include md-screen {
    top: 50%;
  }

  @include md-screen {
    width: 35px;
    height: 35px;
    font-size: 14px;
  }


  @include msm-screen {
    width: 30px;
    height: 30px;
  }

  &:hover {
    border-color: hsl(var(--base));
    background-color: hsl(var(--base));
    color: hsl(var(--black));
  }
}

.slick-next {
  right: -70px;
  color: hsl(var(--white));
  border: 1px solid hsl(var(--white)/.2);

}

.slick-prev {
  left: -70px;

}

.slick-slide img {
  display: inline-block;
}


// slick  Dots
.slick-dots {
  text-align: center;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -70px;

  li {
    display: inline-block;

    button {
      border: 1px solid transparent;
      background-color: hsl(var(--base)/0.1);
      color: hsl(var(--white));
      margin: 0 6px;
      width: 5px;
      height: 14px;
      border-radius: 50%;
      text-indent: -9999px;
      display: block;

    }

    &.slick-active button {
      background-color: hsl(var(--base));
    }
  }
}



// banner-slider 

.banner-slider {
  .slick-prev {
    top: 40%;
    right: -70px;
    left: auto !important;
  }

  .slick-next {
    top: 55%;
  }
}


@media (max-width: 1299px) {

  .banner-slider {
    .slick-prev {
      right: -20px;
    }
  }

  .slick-prev {
    left: -40px !important;
  }

  .slick-next {
    right: -40px;
  }

}

@media (max-width: 768px) {

  .slick-prev {
    left: 0px !important;
  }

  .slick-next {
    right: 0px !important;
  }

}