.hero {
    padding: 240px 0px 80px 0px;
    position: relative;
    overflow: hidden;
    z-index: 1;

    .hero--element {
        top: 0;
        right: 0;
        width: 100%;
        height: 100%;

        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: hsl(var(--black) / 0.2);

        }

        video {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .hero--content {
        .title {
            font-size: 80px !important;
            text-transform: capitalize;
            line-height: 1.25;
            margin-bottom: 60px;

            span {

                -webkit-text-stroke-width: 2px;
                -webkit-text-stroke-color: hsl(var(--base));
                color: transparent;
                transition: all 0.5s;
                position: relative;
                -webkit-text-fill-color: transparent;
                animation: shine 3s infinite !important;
                -webkit-mask-image: linear-gradient(-75deg, rgba(56, 75, 255, 0.15) 50%, #384BFF 50%, rgba(56, 75, 255, 0.15) 100%);
                -webkit-mask-size: 200%;

            }

        }
    }

    .sponser--logo {
        .title {
            &::after {
                content: "";
                position: absolute;
                top: 10px;
                right: 0;
                width: 60%;
                border: 1px solid hsl(var(--white)/ .2);
            }
        }
    }


    .hero--btn {
        border-bottom: 1px solid hsl(var(--base));
        padding-bottom: 5px;

        i {
            margin-left: 3px;
            rotate: -45deg;
            transition: 0.2s linear;
        }

        &:hover {
            i {
                rotate: 0deg;
            }
        }
    }


    .icon--wrap {
        top: 45%;
        right: 2%;

        li {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            border: 1px solid hsl(var(--white)/.2);
        }
    }

}



// svg stroke animation
.svg-stroke--animation {
    stroke: hsl(var(--black)) !important;
    stroke-width: 1px !important;

}

// category list
.hero-category--list {
    margin-bottom: 24px;

    .item {
        a {
            width: 36px;
            height: 36px;
        }
    }
}

@keyframes draw {
    0% {
        stroke-dasharray: 2000;
        stroke-dashoffset: 2000;
    }

    100% {
        stroke-dasharray: 2000;
        stroke-dashoffset: 0;
    }
}


.svg-fill--base {
    fill: hsl(var(--base));
}

.svg-stroke--base-two {
    stroke: hsl(var(--base-two)/0.6);
}



.playground {
    width: 500px;
    height: 600px;
    // overflow: hidden;
    position: absolute;
    bottom: 25%;
    right: 10%;
}

#helper {
    position: fixed;
    width: 100vw;
    height: 100vh;
    pointer-events: none;
    opacity: 0;
    visibility: hidden;
    z-index: -1;
}

.tag {
    &__item {
        border-radius: 16px;
        border: 1px solid hsl(var(--white)/.2);
        position: absolute;
        overflow: hidden;
        width: 70px;
        height: 70px;
    }

    &__link {
        display: block;
        padding: 10px;
        text-align: center;
        background-color: hsl(var(--body-color));
        width: 100%;
        height: 100%;

        img {
            width: 60px;
        }
    }
}



.text-reveal {
    display: inline-block;
    overflow: hidden;
    clip-path: inset(0 100% 0 0);
}