// style one
.section-content {
    .title-wrap {

        .title {
            line-height: 1.18;
        }
    }

    .heading--wrap {
        position: relative;
        z-index: 1;

        &::after {
            content: "";
            position: absolute;
            left: 0;
            bottom: 14px;
            width: 100%;
            height: 10px;
            border-radius: 20px;
            background-color: hsl(var(--base));
            z-index: -1;
        }



        .heading {
            text-transform: uppercase;
            position: relative;

        }
    }

}


// style two
.section-content-2 {
    .title-wrap {
        display: flex;
        justify-content: space-between;

        .title {
            color: hsl(var(--white));
            font-size: 24px !important;
            font-weight: 700;
            line-height: 1.16;
            margin: 0px;
        }
    }
}

// style three
.section-content-3 {
    margin-bottom: 78px;
    .heading{
      padding: 6px 12px;
      border-radius: 32px;
      border: 1px solid hsl(var(--white)/.2);
      text-transform: uppercase;
      margin-bottom: 28px;
      display: inline-block;

    }

    .title {
        font-size: 56px !important;
        font-weight: 700;
        margin-bottom: 34px;
    }


    .section--content-img{
        width: 165px;
        height: 165px;
    }
}

// style four
.section-content-4 {


    .strok--heading {
        position: absolute;
        top: -10px;
        left: -30px;
        color: transparent;
        -webkit-text-stroke: 1px rgba(255, 255, 255, 0.1);
        font-family: Poppins;
        font-size: 60px;
        font-weight: 700;
        line-height: 28px;
        z-index: -1;

        &::after{
            content: "";
            position: absolute;
            bottom: -9px;
            left: 0;
            width: 100%;
            height: 40px;
            background: linear-gradient(180deg, rgba(255, 128, 0, 0) -20%, hsl(var(--body-color)) 100%);
            opacity: 1;
        }

    }

    .title {
        text-align: center;
        line-height: 1.25;
        position: relative;
    }

    .subtitle {
        font-size: 18px;
        font-weight: 400;
        line-height: 24px;
        text-align: center;
    }
}

// style five
.section-content-5 {
    display: flex;
    justify-content: center;
    align-items: center;

    .title {
        color: hsl(var(--black));
        text-align: center;
        font-size: 36px !important;
        font-weight: 700;
        line-height: 1.16;
        margin: 0px;
    }
}


.section-content-6 {
    .heading--wrap {
        position: relative;

        &::after {
            content: "";
            position: absolute;
            left: -16px;
            top: 2px;
            background-color: hsl(var(--white));
            width: 4px;
            height: 13px;


        }


        &::before {
            content: "";
            position: absolute;
            left: -10px;
            top: 2px;
            background-color: hsl(var(--white));
            width: 4px;
            height: 13px;
        }

        .heading {
            text-transform: uppercase;
            letter-spacing: 3.96px;
            position: relative;

            &::after {
                content: "";
                position: absolute;
                right: -16px;
                top: 2px;
                background-color: hsl(var(--white));
                width: 4px;
                height: 13px;


            }


            &::before {
                content: "";
                position: absolute;
                right: -10px;
                top: 2px;
                background-color: hsl(var(--white));
                width: 4px;
                height: 13px;
            }

        }
    }



    .title {
        text-align: center;
        line-height: 54px
    }

    .subtitle {

        font-size: 18px;
        font-weight: 400;
        line-height: 24px;
        text-align: center;
    }
}