.marque--section {
    position: relative;
    z-index: 1;


    .marque---wrap {
        overflow: hidden;
        position: relative;
        top: 0px;

        .marque--slider {
            display: flex;
            position: relative;
            padding: 10px 0px;

            .marquee--item {
                display: flex;
                align-items: center;
                gap: 48px;

                li {
                    &:hover {
                        img {
                            scale: 0.9;
                        }
                    }

                    img {
                        max-width: 160px;
                        height: auto;
                        transition: 0.3s linear;
                    }

                    display: flex;
                    align-items: center;
                }
            }
        }
    }
}


.marque--section-two {
    position: relative;
    z-index: 1;


    .marque---wrap {
        overflow: hidden;
        position: relative;
        top: 0px;

        .marque--slider {
            display: flex;
            position: relative;
            padding: 10px 0px;

            .marquee--item {
                display: flex;
                align-items: center;
                gap: 48px;
                flex-shrink: 0;

                li {


                    display: flex;
                    flex-direction: column;
                    align-items: start;
                    flex-shrink: 0;

                    .number {
                        padding: 6px 18px;
                        border-radius: 32px;
                        border: 1px solid hsl(var(--white) / 0.2);
                        text-transform: uppercase;
                        margin-bottom: 8px;
                        display: inline-block;

                        i {
                            margin-left: 4px;
                            rotate: 45deg;
                            color: hsl(var(--base));
                        }
                    }


                    .marquee-text {
                        font-size: 80px;
                        font-weight: 600;
                        flex-shrink: 0;
                        color: hsl(var(--white)/.2);
                    }

                    img {
                        margin-left: 10em;
                        margin-top: -175px;
                        transform: scale(0);
                        visibility: hidden;
                        transition: all 0.6s;
                        display: inline-block;
                        width: 330px;
                        height: 220px;
                        object-fit: cover;
                    }

                    &:hover {
                        .marquee-text {
                            color: hsl(var(--base));
                        }

                        img {
                            transform: scale(1);
                            visibility: visible;
                        }
                    }
                }
            }
        }
    }
}



.left-marq {
    animation: moveLeft 40s linear infinite;
}

.right-marq {
    animation: moveRight 60s linear infinite;
}

