// blog card
.blog-card {
  position: relative;
  width: 100%;
  transition: 0.2s linear;
  z-index: 1;

  &:hover {
    img {
      scale: 1.1;
    }
  }

  .thumb {
    width: 100%;
    height: 400px;
    overflow: hidden;

    img {
      transition: 0.3s linear;
    }
  }

  .blog-content {
    padding: 20px 0px 0 0;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;

    a:hover {
      .title {
        color: hsl(var(--base));
        transition: 0.2s linear;
        text-decoration: underline !important;
      }
    }
  }

  &.list {
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid hsl(var(--white)/.2);

    .blog-content {
      padding: 0;
    }
  }

}

.col-xl-5 .list:last-child {
  border: 0;
}


// global base card
.base--card {
  padding: 24px;
  border-radius: 12px;
  border: 1px solid hsl(var(--white)/0.2);
}


// testimonial cards
.testimonial-card {
  position: relative;
  z-index: 1;

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 2px;
    width: 0;
    height: 0;
    border-top: 47px solid transparent;
    border-bottom: 0px solid transparent;
    border-right: 37px solid hsl(var(--base));
  }

  &:before {
    content: "";
    width: 0;
    height: 0;
    border-top: 46px solid transparent;
    border-bottom: 0px solid transparent;
    border-right: 37px solid #1b1b1b;
    position: absolute;
    top: 50%;
    left: 4px;
    z-index: 1;
  }

  .item--wrap {
    margin-left: 38px;
    padding: 40px 24px !important;
    border-left: 1px solid hsl(var(--base));
  }

  .description {
    font-size: 16px;
  }

  .icon {
    width: 60px;
    height: 60px;
    top: -30px;
    left: 40px;
  }

  .user--thumb {
    width: 90px;
    height: 90px;
    border-radius: 50%;
    overflow: hidden;
    flex-shrink: 0;
  }

  .quote--img {
    width: 30px;
  }

  .description {
    margin-bottom: 24px;
  }

}


// dashboard profile card
.dashboard_profile-card {
  border-radius: 8px 8px 20px 20px;
  background-color: hsl(var(--white));
  position: relative;

  &::after {
    content: "";
    position: absolute;
    bottom: -8px;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 32px;
    transition: 0.3sease;
    background-color: hsl(var(--black));
    z-index: -1;
  }

  .dashboard_profile_wrap {
    position: relative;

    .profile_photo img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .profile_photo::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: hsl(var(--dark));
    opacity: 0;
    transition: all 0.4s;
    visibility: hidden;
  }

  .profile_photo .photo_upload {
    display: inline-block;
    padding: 0px 0px 0px;
    position: absolute;
    top: 50%;
    left: 50%;
    color: hsl(var(--white));
    transform: translate(-50%, -50%);
  }

  .profile_photo .photo_upload i {
    cursor: pointer;
    visibility: hidden;
    opacity: 0;
  }

  .contact-info {
    padding: 0px 20px;
    width: 100%;

    .info-wrap {
      padding: 20px 0px;
      border-bottom: 1px dashed hsl(var(--base-two) / 0.5);
      color: hsl(var(--black));

      .info {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        gap: 12px;
        margin-bottom: 8px;
      }
    }
  }


  .user-profile {
    position: relative;
    overflow: hidden;
    padding: 20px;
    background: hsl(var(--base));
    border-radius: 8px 8px 0px 0px;
  }

  .dashboard_profile_wrap {
    position: relative;

    .profile-details ul li .user-name {
      font-weight: 700;
      font-size: 16px;
      color: hsl(var(--white));
    }

    .profile_photo {
      height: 100px;
      width: 100px;
      border-radius: 50%;
      overflow: hidden;
      position: relative;
      left: 50%;
      transform: translateX(-50%);
    }
  }

}


// key card
.key-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .count-number-wraper {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    background: var(--gradient-bg);
    height: 70px;
    width: 70px;
    margin-bottom: 40px;

    span {
      color: hsl(var(--white));
      font-size: 40px;

    }
  }

  .title {
    margin-bottom: 24px;
    font-size: 24px !important;
    font-weight: 700;
    line-height: 1.29;
  }

  .subtitle {
    width: 80%;
    text-align: center;
  }
}


//Dashboard wizard
.wizard-card {
  padding: 20px;
  border-radius: 8px;
  background-color: hsl(var(--section-bg));
  border: 1px solid hsl(var(--white) / 0.15);

  .content-wrap {
    .icon-wrap {
      width: 39px;
      height: 39px;
      border-radius: 6px;
      background-color: hsl(var(--white) / 0.07);

      &:hover {
        cursor: grab;
      }

      &:active {
        cursor: grabbing;
      }

      &::after {
        content: "";
        position: absolute;
        width: 40px;
        height: 40px;
        border-radius: 4px;
        background-color: hsl(var(--white)/0.1);
        bottom: 0px;
        right: 0px;
        z-index: -1;
      }

      i {
        color: hsl(var(--white));
      }
    }

    .title {
      font-size: 18px;
    }
  }

  .amount-wrap {
    .amount {
      font-size: 20px;
      font-weight: 500;
    }
  }
}


.contact--box {
  border: 1px solid hsl(var(--white) / 0.2);
  border-radius: 6px;
  padding: 28px 24px;
  width: 100%;

  .icon-wrap {
    color: hsl(var(--base));
    font-size: 54px;
    margin-bottom: 30px;

  }

}

// why choose card
.whychoose--card {
  border: 1px solid hsl(var(--black)/0.8);
  background-color: hsl(var(--white));
  border-radius: 32px;
  padding: 70px 48px;

  &::after {
    content: "";
    position: absolute;
    bottom: -8px;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 32px;
    transition: 0.3s ease;
    background-color: hsl(var(--black));
    z-index: -1;
    opacity: 0;
    visibility: hidden;
  }

  .icon--wrap {
    width: 74px;
    height: 74px;
    flex-shrink: 0;
    border-radius: 24px;
    background-color: hsl(var(--base));

    i {
      font-size: 38px;
    }
  }

  .title {
    line-height: 1.5;
  }


  &:hover {
    &::after {
      visibility: visible;
      opacity: 1;
    }
  }

}

// review card
.review-card {
  display: flex;
  flex-direction: column;
  gap: 12px;

  .user-info {
    display: flex;
    align-items: center;
    gap: 12px;

    .thumb-wrap {
      width: 49.51px;
      height: 49.51px;
      border-radius: 50%;
      overflow: hidden;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

    }

    .user-name {
      .name {
        font-size: 16px;
        font-weight: 500;
        line-height: 1.18;
        margin: 0px;
      }

      .rating-wrap {
        display: flex;
        align-items: center;
        gap: 4px;

        li {
          i {
            color: hsl(var(--yellow));
          }

          &:last-child {
            margin-left: 8px;

            p {
              font-size: 14px;
              font-weight: 400;
              line-height: 1.5;
            }
          }
        }
      }
    }
  }
}

// brand-logo
.brand-logo--wrap {
  img {
    opacity: 0.4;
    transition: 0.3s ease-in-out;
  }

  &:hover {
    img {
      opacity: 1;
    }
  }
}



.ui-sortable-placeholder {
  background-color: #ddd !important;
  visibility: visible !important;
  border-radius: 8px;
}


.contact--profile-box {
  width: 450px !important;

  .contact-info--box {
    border: 1px solid hsl(var(--black) / 0.3);
    padding: 20px;

    &::after {
      content: "";
      position: absolute;
      bottom: -8px;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 32px;
      transition: 0.3s ease;
      background-color: hsl(var(--black));
      z-index: -1;

    }
  }
}



.plan--card {
  border: 1px solid hsl(var(--white) / 0.2);
  border-radius: 12px;
  padding: 60px 36px !important;
  height: 100%;
  justify-content: space-between;


  .details--wrap {
    height: 400px;
  }

  .feature--wrap {
    border-top: 1px solid hsl(var(--white) / 0.2);
    border-bottom: 1px solid hsl(var(--white) / 0.2);
    margin-top: 40px;
    padding-top: 40px;
    margin-bottom: 40px;
    padding-bottom: 40px;
    height: 100%;
  }

  .feature {
    i {
      font-size: 18px;
    }
  }


  &.two {
    justify-content: start;
    align-items: center;
    padding: 0 !important;
    transition: 0.3s linear;

    &:hover {
      border: 1px solid hsl(var(--base));

      .heading--wrap {
        border-right: 1px solid hsl(var(--base));
      }
    }

    .heading--wrap {
      width: 35%;
      border-bottom: 0;
      border-right: 1px solid hsl(var(--white) / 0.2);
      height: 100%;
      margin: 0;
      padding: 40px;
      transition: 0.3s linear;
    }

    .details--wrap {
      margin: 0 !important;
      padding: 40px 40px 40px 60px;
      width: 100%;
      height: 100% !important;
    }

    .feature--wrap {
      margin: 0;
      padding: 0;
      border-bottom: 0;
      border-top: 0;
    }

    .price--wrap {
      flex-shrink: 0;
    }
  }
}


.staff--card {
  background-color: hsl(var(--body-color));
  padding: 52px 20px;
  border-radius: 6px;

  border: 1px solid transparent;
  transition: 0.3s linear;

  &:hover {
    border: 1px solid hsl(var(--base));
  }


  .thumb--wrap {
    width: 242px;
    height: 242px;
    border-radius: 100%;
  }

  .link {
    a i {
      transition: 0.2s linear;
    }

    &:hover {
      a i {
        color: hsl(var(--base));
      }
    }
  }
}


.case-study--card {

  .thumb--wrap {
    width: 100%;
    height: 330px;
    margin-bottom: 30px;
    border-radius: 16px;
    overflow: hidden;

    img {
      transition: 0.3s linear;
    }

    &:hover {
      img {
        scale: 1.1;
      }
    }
  }


  .case--btn {
    position: absolute;
    right: -7px;
    bottom: -7px;
    font-size: 25px;
    background-color: hsl(var(--white));
    width: 80px;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 7px solid hsl(var(--section-bg));
    border-radius: 20px;

    i {
      color: hsl(var(--black));
      rotate: -35deg;
      margin-top: 3px;
    }

    &::before {
      position: absolute;
      bottom: 0px;
      left: -27px;
      content: "";
      width: 20px;
      height: 30px;
      box-shadow: hsl(var(--section-bg)) 7px 8px 0px 0px;
      background: transparent;
      border-radius: 0% 8% 70% 10%;
    }

    &::after {
      top: -28px;
      right: -0px;
      position: absolute;
      content: "";
      width: 30px;
      height: 23px;
      box-shadow: hsl(var(--section-bg)) 11px 4px 0px 1px;
      background: transparent;
      border-radius: 0% 10% 70%;
    }

  }

  .tag--wrap {
    margin-bottom: 20px;
  }

  &:hover {

    .case--btn {
      background-color: hsl(var(--base));

      i {
        animation: toRight 0.3s forwards;
      }
    }
  }


}




.service--card {
  background-color: hsl(var(--section-bg));
  padding: 40px;
  border-radius: 6px;
  border: 1px solid transparent;
  transition: 0.3s linear;

  &:hover {
    border: 1px solid hsl(var(--base));
  }

  &.two {
    padding: 24px;
  }

  &.three {
    padding: 24px;
    background-color: hsl(var(--body-color));
  }

  .number {
    top: 30px;
    right: 30px;
    font-size: 50px;
    font-weight: 600;
    -webkit-text-fill-color: hsl(var(--section-bg));
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: hsl(var(--white) / 0.1);
    transition: 0.2s linear;
  }

  .icon--wrap {
    i {
      font-size: 50px;
      transition: 0.2s linear;
    }
  }

  &:hover {
    .icon--wrap {
      i {
        color: hsl(var(--base));
      }
    }

    .number {
      -webkit-text-stroke-color: hsl(var(--base));
    }
  }

}

.discover--box {
  border-radius: 6px;
  border: 1px solid hsl(var(--white) / 0.2);
  padding: 72px;
  background-color: hsl(var(--section-bg));
}


.staff-details--card {
  .thumb--wrap {
    width: 450px;
    border-radius: 15px;
    height: 464px;
    overflow: hidden;
  }

  .contact--info {
    padding: 20px 0;
    margin: 20px 0;
    border-top: 1px solid hsl(var(--white) / 0.2);
    border-bottom: 1px solid hsl(var(--white) / 0.2);

  }

  .icon--wrap .icon {
    width: 42px;
    height: 42px;
    border-radius: 50%;
    border: 1px solid hsl(var(--white) / 0.1);

    a i {
      font-size: 20px;
    }
  }
}







.content-details--wrap {
  ul li {
    list-style: disc;
    margin-left: 20px;
    color: hsl(var(--white) / 0.75);
  }
}



.service--thumb-wrap {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 70vh;
  display: flex;
  position: sticky;
  top: 250px;
  margin-top: 10px;

  img {
    width: 100%;
    position: absolute;
    border-radius: 12px;
    height: 35em;
  }
}


.content-details {
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: 70vh;
  display: flex;

  &:last-child {
    height: 140vh;
  }
}



.content-details--wrap h6,
.content-details--wrap p,
.content-details--wrap ul,
.content-details--wrap li {
  transition: color 0.2s ease;
}



.timeline-section {
  position: relative;
  z-index: 1;
}


.progress--bar {
  position: absolute;
  top: 0;
  left: 27px;
  width: 2px;
  height: 0;
  background-color: hsl(var(--base));
  z-index: -1;
}

.timeline-item {
  position: relative;
  padding-left: 60px;
  margin-bottom: 60px;
}

.timeline-item .icon {
  position: absolute;
  left: 10px;
  top: 0;
  font-size: 20px;
  color: hsl(var(--white));
  z-index: 1;
  background-color: #2f2f2f;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.timeline-item .icon.active {
  color: hsl(var(--base));
}

.timeline-item h3 {
  margin: 0 0 10px;
  font-size: 22px;
}

.timeline-item p {
  margin: 0;
  font-size: 16px;
}


.map-section .map-box {
  height: 586px;
  overflow: hidden;
  border-radius: 6px;
}

.map-section .map-box iframe {
  width: 100%;
  height: 100%;
}


.progress--container {


  .progress,
  .progress-stacked {
    --bs-progress-height: 10px;
    --bs-progress-border-radius: 0;
    --bs-progress-bar-bg: hsl(var(--base));
  }

}