// base modal design

.modal--base {

    .modal-dialog {
        .modal-content {
            border-radius: 20px !important;
            border: 1px solid hsl(var(--white)/0.3);
            padding: 24px;
            position: relative;

            background-color: hsl(var(--body-color));


            &::after {
                content: "";
                position: absolute;
                bottom: -8px;
                left: 0;
                width: 100%;
                height: 100%;
                border-radius: 32px;
                transition: 0.3s ease;
                background-color: hsl(var(--base));
                z-index: -1;
              }
        }
        .modal-header {
            justify-content: space-between;
            padding: 0px;
            padding-bottom: 16px;
            margin-bottom: 36px;

            .btn--close {
                width: 30px;
                height: 30px;
                border-radius: 8px;
                background-color: hsl(var(--white)) !important;
                color: hsl(var(--danger));
            }
        }
        .modal-body{
            padding: 0px;
        }

        .modal-footer{
            border: 0;
        }
    }
}
